import React, { useState, useEffect } from 'react'
import { faAngleLeft, faAngleRight } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {TransitionGroup, CSSTransition} from 'react-transition-group';

import { Carousel, CarouselImage, CarouselBtnContainer, CarouselBtn } from '../../styles/Home'

function CarouselImages({ imgs }) {
    
    const [currentImg, setCurrentImg] = useState(1)

    console.log(imgs[currentImg - 1])

    const gotoLeft = () => {
        if(currentImg !== 1){  setCurrentImg(currentImg -1) }
        else{ setCurrentImg(imgs.length) }
    }

    const gotoRight = () => {
        if(currentImg !== imgs.length){ setCurrentImg(currentImg +1) }
        else{ setCurrentImg(1) }
    }

    useEffect(() => {
    }, [currentImg])

    return (
        <>
        <Carousel>
            <TransitionGroup className="carousel-image-container">
                <CSSTransition
                        classNames={'fade'}
                        timeout={{ enter: 500, exit: 500 }}
                        key={Math.random().toString()}
                    >
                        <CarouselImage 
                            img={imgs[currentImg - 1]}
                        />
                    </CSSTransition>
                
            </TransitionGroup>
            <CarouselBtnContainer>
                <CarouselBtn onClick={gotoLeft}>
                    <FontAwesomeIcon icon={faAngleLeft} color="rgb(0,47,91)" size="2x"/>
                </CarouselBtn>
                <CarouselBtn onClick={gotoRight}>
                    <FontAwesomeIcon icon={faAngleRight} color="rgb(0,47,91)" size="2x"/>
                </CarouselBtn>
            </CarouselBtnContainer>
        </Carousel>
        </>
    )
}

export default CarouselImages
