import styled from 'styled-components'

export const MainContainer = styled.div`
    width: 100%;
    height: 100%;
    overflow-x: hidden;
`

export const TopBarContainer = styled.div`
    width: 75%;
    height: 5%;
    background: linear-gradient(90deg, rgb(0,47,91), rgb(0,47,91) 45%, rgb(83,24,140) 55%, rgb(83,24,140));
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 10%;
    padding-left: 15%;
`

export const TopBarText = styled.span`
    color: white;
    margin-left: 1rem;
`

export const LoginLabel = styled.a`
    padding: 0 1rem;
    height: 100%;
    background-color: rgb(178,223,252);
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgb(0,47,91);
    text-decoration: none;
    font-weight: bold;
`
export const TitleContainer = styled.div`
    width: 80%;
    height: 15%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10%;
`

export const TitleHomeContainer = styled.div`
    display: flex;
    align-items: center;
    flex-direction: row;
    width: 30%;
`

export const TitleHome = styled.h1`
    display: flex;
    align-items: center;
    justify-content: center;
    color: ${props => props.color};
    font-size: ${props => props.fontSize};
    font-weight: 50;
    margin-left: ${props => props.marginLeft || 0};
    margin: 0;
`

export const ButtonCalendar = styled.button`
    width: 10%;
    height: 30%;
    background-color: white;
    border: solid 1px rgb(178,223,252);
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-weight: 50;
    margin-left: ${props => props.marginLeft ? props.marginLeft : 0};
    padding: 0;
    &:hover{ cursor: pointer; }
    &:focus{ outline: none }
    span{
        color: rgb(0,47,91);
        font-size: 1.3rem;
        font-weight: bold;
    }
`

export const Carousel = styled.div`
    width: 100%;
    height: 50%;
    display: flex;
    align-items: center;
    flex-direction: column;
`

export const CarouselImageContainer = styled.div`
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: nowrap;
    
`

export const CarouselImage = styled.div`
    width: 100%;
    height: 100%;
    background-image: url(${props => props.img});
    background-repeat: no-repeat;
    background-size: 100% 100%;
    display: flex;
    flex-shrink: 0;
    &.fade-enter{ opacity: 0; }
    &.fade-enter-active{
        opacity: 1;
        transition: opacity 500ms ease-in-out;
    }
    &.fade-exit{ opacity: 1; }
    &.fade-exit-active{
        opacity: 0;
        transition: opacity 500ms ease-in-out;
    }
`

export const CarouselBtnContainer = styled.div`
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    position: relative;
    top: -55%;
    top: calc(-50% - 1.5rem);
    margin-bottom: -3rem;
`

export const CarouselBtn = styled.button`
    width: 3rem;
    height: 3rem;
    border-radius: 1.5rem;
    border: 0;
    background-color: rgba(255,255,255,0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 2rem;
    &:hover{ cursor: pointer; }
    &:focus{ outline: none }
`

export const InfoContainerRow = styled.div`
    width: 80%;
    height: 50%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
`

export const InfoContainer = styled.div`
    align-items: center;
    width: 20%;
    justify-content: center;
    display: flex;
    flex-direction: column;
`

export const ImageInfo = styled.img`
    width: 10rem;
    height: 10rem;
    border-radius: 5rem;
`

export const TextInfo = styled.p`
    width: 5rem;
    height: 5rem;
    border-radius: 2.5rem;
    width: 100%;
    text-align: center;
    color: rgb(0,47,91);
`

export const TitleInfo = styled.h2`
    color: rgb(0,47,91);
`

export const LoginContainer = styled.div`
    width: 90%;
    height: 70%;
    background: linear-gradient(180deg, rgb(0,47,91), rgb(0,47,91) 50%, rgb(178,223,252) 50%, rgb(178,223,252));
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: 10%;
`

export const LoginInfoContainer = styled.div`
    width: ${props => props.width};
    height: ${props => props.height};
    background-color: ${props => props.bgColor};
    display: flex;
    flex-direction: ${props => props.flexDirection};;
    align-items: ${props => props.start ? props.flexDirection === 'row' ? 'center' : 'flex-start' : 'center'};
    justify-content: ${props => props.start ? props.flexDirection === 'row' ? 'flex-start' : 'center' : 'center'};;
    border: 0;
`

export const LoginInfoContainerText = styled.p`
    color: ${props => props.color};
    margin: 0;
    margin-left: 2rem;
    width: 80%;
`

export const LoginInfoContainerTitle = styled.h3`
    color: ${props => props.color};
    font-size: 1.7rem;
    margin: 1rem 0;
    margin-left: 2rem;
`

export const LoginFormContainer = styled.h3`
    width: 15%;
    height: 80%;
    margin-left: 5%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
`

export const LoginForm = styled.div`
    width: 100%;
    height: 70%;
    background-color: rgb(236,236,236);
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-top: 1rem;
    padding-bottom: 1rem;
`

export const LoginTitle = styled.h4`
    color: rgb(0,47,91);
    margin-top: 0.5rem;
    margin-bottom: 1rem;
    text-align: center;
`

export const LoginFormLabel = styled.label`
    color: rgb(100,100,100);
`

export const LoginInput = styled.input`
    width: 80%;
    background-color: white;
    border-radius: 0.5rem;
    border: 0;
    text-align: center;
    margin: 0.5rem 0;
    padding: 0.5rem;
    &:focus{ outline: 0 }
`

export const RememberPasswordContainer = styled.div`
    width: 100%;
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    align-items: center;
`

export const LoginFormLabelCheckbox = styled.label`
    color: rgb(100,100,100);
    font-size: 1rem;
    text-align: center;
`

export const LoginFormCheckbox = styled.input`
    background-color: white;
    width: 1rem;
    height: 1rem;
    margin: 0 1rem;
`

export const LoginFormBtn = styled.button`
    background-color: rgb(0,47,91);;
    width: 70%;
    height: 10%;
    border-radius: 0.5rem;
    border: 0;
    color: white;
    margin-top: 1rem;
    padding: 0.3rem 0;
    &:hover{ cursor: pointer; }
    &:focus{ outline: none }
`

export const LoginFormForgotPasswordContainer = styled.div`
    width: 70%;
    padding-top: 1rem;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
`

export const LoginFormForgotPassword = styled.a`
    color:  rgb(100,100,100);
    font-size: 1rem;
    margin-bottom: 0.5rem;
    text-decoration: none;
    text-align: center;
`

export const DiscountAnnouncement = styled.div`
    background: linear-gradient(135deg, rgb(0,47,91), rgb(0,47,91) 25%, rgb(83,24,140) 75%, rgb(83,24,140));
    width: 100%;
    height: 20%;
    display: flex;
    flex-direction:column;
    align-items: center;
    justify-content: center;
`

export const DiscounntTextBold= styled.h4`
    color: white;
    font-size: 1.4rem;
    margin: 0;
    text-align: center;
`


export const DiscounntText= styled.p`
    color: white;
    font-size: 0.7rem;
    margin: 0.2rem 0;
    text-align: center;
`