import React from 'react'
import { faPhoneAlt } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { TopBarContainer, TopBarText, LoginLabel } from '../../styles/Home'

function TopBar() {
    return (
        <TopBarContainer>
            <div>
                <FontAwesomeIcon icon={faPhoneAlt} color="white" />
                <TopBarText>ATENCIÓN 24 HRS (871) 480 2907</TopBarText>
            </div>
            <LoginLabel href="/login">ACCESO CLIENTE</LoginLabel>
        </TopBarContainer>
    )
}

export default TopBar
