import React from 'react'
import { faUserMd, faPercent, faVials, faBriefcaseMedical } from "@fortawesome/free-solid-svg-icons";
import { faFacebook, faInstagram } from "@fortawesome/free-brands-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { LoginInfoContainer, LoginInfoContainerText, LoginInfoContainerTitle, TitleHome } from '../../styles/Home'

function LoginInfo() {
    return (
        <LoginInfoContainer
            width="70%"
            height="80%"
            flexDirection="row"
        >
            <LoginInfoContainer
                width="30%"
                height="100%"
                flexDirection="column"
            >
                <LoginInfoContainer
                    width="100%"
                    height="50%"
                    bgColor="rgb(255,201,7)"
                    flexDirection="column"
                    start
                >
                    <FontAwesomeIcon icon={faUserMd} color="rgb(0,47,91)" size="3x" style={{ marginLeft: '2rem' }}/>
                    <LoginInfoContainerTitle color="rgb(0,47,91)">MÉDICOS</LoginInfoContainerTitle>
                    <LoginInfoContainerText color="rgb(0,47,91)">Lorem ipsum dolor sit amet, consectetur adipiscing elit.</LoginInfoContainerText>
                </LoginInfoContainer>
                <LoginInfoContainer
                    width="100%"
                    height="50%"
                    bgColor="rgb(0,47,91)"
                    flexDirection="column"
                    start
                >
                    <FontAwesomeIcon icon={faPercent} color="rgb(178,223,252)" size="3x" style={{ marginLeft: '2rem' }}/>
                    <LoginInfoContainerTitle color="white">DESCUENTOS</LoginInfoContainerTitle>
                    <LoginInfoContainerText color="white">Lorem ipsum dolor sit amet, consectetur adipiscing elit.</LoginInfoContainerText>
                </LoginInfoContainer>
            </LoginInfoContainer>
            <LoginInfoContainer
                width="70%"
                height="100%"
                flexDirection="column"
            >
                <LoginInfoContainer
                    width="100%"
                    height="75%"
                    flexDirection="row"
                >
                    <LoginInfoContainer
                        width="30%"
                        height="100%"
                        flexDirection="row"
                        bgColor="rgb(178,223,252)"
                        flexDirection="column"
                        start
                    >
                        <LoginInfoContainer
                            width="100%"
                            flexDirection="row"
                            flexDirection="row"
                        >
                            <TitleHome color="rgb(0,47,91)" fontSize="6rem">M</TitleHome>
                            <TitleHome color="rgb(83,24,140)" fontSize="6rem">D</TitleHome>
                        </LoginInfoContainer>
                        <LoginInfoContainer
                            width="100%"
                            flexDirection="row"
                            flexDirection="column"
                        >
                            <TitleHome color="rgb(0,47,91)" fontSize="2rem">MÉDICA</TitleHome>
                        </LoginInfoContainer>
                    </LoginInfoContainer>
                    <LoginInfoContainer
                        width="70%"
                        height="100%"
                        flexDirection="column"
                    >
                        <LoginInfoContainer
                            width="100.1%"
                            height="50%"
                            bgColor="rgb(83,24,140)"
                            flexDirection="column"
                            start
                        >
                            <FontAwesomeIcon icon={faBriefcaseMedical} color="rgb(178,223,252)" size="3x" style={{ marginLeft: '2rem' }}/>
                            <LoginInfoContainerTitle color="white">SERVICIOS</LoginInfoContainerTitle>
                            <LoginInfoContainerText color="white">Lorem ipsum dolor sit amet, consectetur adipiscing elit.</LoginInfoContainerText>
                        </LoginInfoContainer>
                        <LoginInfoContainer
                            width="100.1%"
                            height="50%"
                            bgColor="rgb(0,47,91)"
                            flexDirection="column"
                        >
                            <LoginInfoContainerTitle color="white">CONTACTO</LoginInfoContainerTitle>
                            <LoginInfoContainer
                                width="100%"
                                bgColor="rgb(0,47,91)"
                                flexDirection="row"
                            >
                                <FontAwesomeIcon icon={faFacebook} color="white" size="3x" style={{ marginLeft: '2rem' }}/>
                                <FontAwesomeIcon icon={faInstagram} color="white" size="3x" style={{ marginLeft: '2rem' }}/>
                            </LoginInfoContainer>
                        </LoginInfoContainer>
                    </LoginInfoContainer>
                </LoginInfoContainer>
                <LoginInfoContainer
                    width="100%"
                    height="36%"
                    bgColor="rgb(83,24,140)"
                    flexDirection="row"
                    start
                >
                    <FontAwesomeIcon icon={faVials} color="rgb(178,223,252)" size="4x" style={{ marginLeft: '2rem' }}/>
                    <LoginInfoContainer
                        width="70%"
                        height="100%"
                        flexDirection="column"
                        start
                    >
                        <LoginInfoContainerTitle color="white">LABORATORIO</LoginInfoContainerTitle>
                        <LoginInfoContainerText color="white">Lorem ipsum dolor sit amet, consectetur adipiscing elit.</LoginInfoContainerText>
                    </LoginInfoContainer> 
                </LoginInfoContainer>
            </LoginInfoContainer>
        </LoginInfoContainer>
    )
}

export default LoginInfo
