import React from 'react'
import { faCalendarAlt } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { TitleContainer, TitleHome, ButtonCalendar, TitleHomeContainer } from '../../styles/Home'

function Title() {
    return (
        <TitleContainer>
            <div></div>
            <TitleHomeContainer>
                <TitleHome color="rgb(0,47,91)" fontSize="7rem">M</TitleHome>
                <TitleHome color="rgb(83,24,140)" fontSize="7rem">D</TitleHome>
                <TitleHome color="rgb(0,47,91)" fontSize="5rem" marginLeft="1rem">MÉDICA</TitleHome>
            </TitleHomeContainer>            
            <ButtonCalendar onClick={() => console.log('a')}>
                <span>CITA</span>
                <FontAwesomeIcon icon={faCalendarAlt} color="rgb(0,47,91)" size="2x"/>
            </ButtonCalendar>
        </TitleContainer>
    )
}

export default Title
