import React, { useState } from 'react'

import TopBar from '../components/home/TopBar'
import Title from '../components/home/Title'
import Info from '../components/home/Info'
import LoginInfo from '../components/home/LoginInfo'
import LoginForm from '../components/home/LoginForm'
import CarouselImages from '../components/home/CarouselImages'

import { MainContainer, LoginContainer } from '../styles/Home'

const img1 = require('../assets/img/img1.jpg')
const img2 = require('../assets/img/img2.jpg')
const img3 = require('../assets/img/img3.png')

function Home() {

    const [imgs, setImgs] = useState([img2, img1, img3])

    return (
        <MainContainer>
            <TopBar />
            <Title />
            <CarouselImages imgs={imgs}/>
            <Info />
            <LoginContainer>
                <LoginInfo />
                <LoginForm />
            </LoginContainer>
        </MainContainer>
    )
}

export default Home
